import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeLayoutComponent } from './pages/home-layout/home-layout.component';
import { LuxuryRoomsComponent } from './pages/reservations/luxury-rooms/luxury-rooms.component';
import { LuxPaymentComponent } from './pages/reservations/payment-portal/lux-payment/lux-payment.component';
import { PayNowComponent } from './pages/payment-folder/pay-now/pay-now.component';
import { PayAtHotelComponent } from './pages/payment-folder/pay-at-hotel/pay-at-hotel.component';
import { HeaderLayoutComponent } from './pages/header-layout/header-layout.component';
import { DashboardComponent } from './pages/dashboard/dashboard.component';
import { NoRouteComponent } from './pages/no-route/no-route.component';

const routes: Routes = [
  {
    path: '', component: HomeLayoutComponent
  },
  {
    path: 'luxury', component: LuxuryRoomsComponent
  },
  {
    path: 'luxpay', component: LuxPaymentComponent
  },
  {
    path: 'payNow', component: PayNowComponent
  },
  {
    path: 'payAtHotel', component: PayAtHotelComponent
  },
  {
    path: 'dashboard', component: DashboardComponent
  },
  {
    path: '**', component: NoRouteComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
